import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { startWith, map } from "rxjs/operators";
import {
  FormControl,
  FormGroup,
  FormBuilder,
  Validators
} from "@angular/forms";
import { CompanyService } from "../../service/company.service";
import { HomeService } from "src/app/service/home.service";
import { DetailedReportService } from "src/app/service/detailed-report.service";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.scss"]
})
export class HomeComponent implements OnInit {
  data: any;
  pieChartOptions: any;
  company = [];
  filteredcompany: Observable<string[]>;
  companycontrol = new FormControl();
  companyCard = [];
  term: string = "";

  // company top list
  companytop = []; // company top list
  companytopform = new FormControl(); //formcontrol html
  filteredcompanylist: Observable<string[]>; //let
  currentempy = [];
  currentemplist = new FormControl();
  curemp = [];
  currentcontrol = new FormControl();
  profileForm: FormGroup;
  companyList: any;
  id: any;

  constructor(
    private router: Router,
    private companySvc: CompanyService,
    private fb: FormBuilder,
    private http: HttpClient,
    private homeService: HomeService
  ) {
    this.data = {
      events: [],
      labels: ["Female", "Male"],
      datasets: [
        {
          data: [25, 75],
          backgroundColor: ["#148fd5", "#da1b63"]
        }
      ]
    };
    this.pieChartOptions = {
      legend: {
        display: false
      },
      tooltips: {
        enabled: true
      }
    };
  }

  ngOnInit() {
    this.companyNames();
    this.companyToplist();
    // this.currentEmp();

    this.filteredcompany = this.companycontrol.valueChanges.pipe(
      startWith(""),
      map(value => this._filter(value))
    );

    this.profileForm = this.fb.group({
      company: ["", Validators.required]
    });
  }

  private _filter(value: string): string[] {
    const filterValue = this._normalizeValue(value);
    return this.company.filter(company =>
      this._normalizeValue(company).includes(filterValue)
    );
  }

  private _normalizeValue(value: string): string {
    return value.toLowerCase().replace(/\s/g, "");
  }

  companyNames() {
    this.homeService.getCompanyNames().subscribe((data: any) => {
      console.log(data);
      if (data) {
        this.companyList = data.data;
        this.companyCard = data.data;
        for (let company of data.data) {
          this.company.push(company.data.companyName);
        }
        console.log(this.company);
      }
    });
  }

  companyToplist() {
    this.homeService.getCompanyToplist().subscribe((data: any) => {
      console.log(data);
      if (data) {
        for (let companytop1 of data.data) {
          this.companytop.push(companytop1.companyName); //companyName is api name
        }
        console.log(this.companytop);
      }
    });
  }

  shouldOpenSideMenu = false;
  myplaceHolder = "";
  companyName = "";

  curforEmp() {
    if (!this.company.includes(this.currentcontrol.value)) {
      this.router.navigate(["/new-company"]);
      console.log("ok");
    } else {
      this.router.navigate(["/company-details"]);
      console.log("not");
    }
  }

  //company.service
  searchCompany() {
    this.companySvc.companayName = this.companycontrol.value;
    console.log(this.companycontrol.value);
    console.log(this.company);
    if (this.company.includes(this.companycontrol.value)) {
      this.id = this.companyList.filter(
        res => res.data.companyName === this.companycontrol.value
      )[0].id;
      this.router.navigate(["/detailed-report"]);
      this.companySvc.setParticularCompanyId(this.id);
    } else {
      this.router.navigate(["/request-company"]);
    }
  }
  showSideMenu() {
    this.shouldOpenSideMenu = !this.shouldOpenSideMenu;
  }
}
